





























































































































import Footer from '@/components/footer/Footer'
import Header from '@/components/header/Header'
import proj from "assets/data/project";
import {
  CBox,
  CFlex,
  CText,
  CStack,
  CHeading,
  CModal,
  CModalOverlay,
  CModalContent,
  CModalHeader,
  CModalFooter,
  CModalBody,
  CModalCloseButton,
  CButton,
  CTag,
  CLink,
  CTagLabel,
  CIcon
} from '@chakra-ui/vue'

export default {
  name: 'Work',
  components: {
    Header,
    Footer,
    CBox,
    CText,
    CStack,
    CFlex,
    CHeading,
    CModal,
    CModalOverlay,
    CModalContent,
    CModalHeader,
    CModalFooter,
    CModalBody,
    CModalCloseButton,
    CButton,
    CTag,
    CLink,
    CTagLabel,
    CIcon
  },
  data () {
    return {
      isOpen: false,
      currentSelectedProj: undefined
    }
  },
  computed: {
    displayProjectDetail () {
      return this.currentSelectedProj
    }
  },
  methods: {
    open (id) {
      this.currentSelectedProj = proj.find(
        element => {
          return element.id === id
        }
      )
      this.isOpen = true
    },
    close () {
      this.currentSelectedProj = undefined
      this.isOpen = false
    }
  }
}
